export class MapUtil {
    /** 将Map转为Object，便于序列化。（因为JSON.stringfy(Map)返回的值总为{}） */
    static convertMapToObject(map: Map<String, any>) {
        if (!map) {
            return null;
        }
        let obj = Object.create(null);
        map.forEach((value, key: any) => {
            obj[key] = value;
        })
        return obj;
    }

    /** 将Object转为Map，因为JSON.parse无法识别类型，反序列化后只能是Object类型 */
    static convertObjectToMap(obj: any): Map<string, any> {
        if (obj === undefined || obj === null) {
            return null;
        }

        let strMap = new Map();
        for (let k of Object.keys(obj)) {
            strMap.set(k, obj[k]);
        }
        return strMap;
    }
}