import { ChangeDetectorRef, Component, EventEmitter, HostBinding, Injector, Input, OnInit, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { DataColumn } from '@farris/ui-common/column';
import { DialogComponent } from '@farris/ui-dialog';
import { MessagerService } from '@farris/ui-messager';
import { NotifyService } from '@farris/ui-notify';
import { ChgdrService } from '../service/chgdr.service';
import { ChangeDataHeader } from '../model/change-data-header';
import { ChangeDataQueryResult } from '../model/change-data-query-result';
import { OperateType } from '../model/operate-type';
import { ErrorUtil } from '../util/error.util';
import { ChgLogConfigField } from '../model/chgdr-config-field';
import { tap } from 'rxjs/operators';
import { forkJoin, Observable, of } from 'rxjs';
import { MapUtil } from '../util/map.util';
import { ChgdrMetadataService } from '../service/chgdr-metadata.service';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { GSPMetadataRTService } from '@gsp-lcm/metadatart-selector';
import { GSPCommonElement } from '@gsp-bef/gsp-cm-metadata';
import { ChgdrInnerService } from '../service/chgdr-inner.service';
import { ChgdrConfigHandler } from '../service/chgdr-config-handler';
import { I18nUtil } from '../service/i18n-service';
import { BeforeShowChangeDetailEvent } from '../model/chgdr-ui-config';

@Component({
  selector: 'lib-chgdr-header-datagrid',
  templateUrl: './chgdr-header-datagrid.component.html',
  styleUrls: ['./chgdr-header-datagrid.component.css']
})
export class ChgdrHeaderDatagridComponent implements OnInit {
  @HostBinding('class.f-utils-fill') baseCls = true;
  @Input() chgdrs: ChangeDataQueryResult;
  @Input() showDataId: boolean = true;

  @Input()
  /** 打开详情前的回调 */
  beforeShowChangeDetail: (event: BeforeShowChangeDetailEvent) => Observable<boolean>;

  headers: ChangeDataHeader[] = [];
  dataCodeFields: ChgLogConfigField[] = [];
  private chgdrConfigHandler: ChgdrConfigHandler;
  @Output()
  pageChanged: EventEmitter<number> = new EventEmitter();
  @Output()
  pageSizeChanged: EventEmitter<number> = new EventEmitter();

  // 获取定义的单元格模板
  @ViewChild('opCell') opCell: TemplateRef<any>;
  @ViewChild("chgdrInfoDialog")
  chgdrInfoDialog: DialogComponent;

  columns: any[];

  currentChgdrData: ChangeDataHeader;
  I18nUtil: I18nUtil;

  constructor(private chgdrService: ChgdrService,
    private messageService: MessagerService,
    private notifyService: NotifyService,
    private ref: ChangeDetectorRef,
    private chgdrInnerService: ChgdrInnerService,
    private injector: Injector) {
  }

  ngOnInit() {
    this.resetColumnAndData(null, []);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!changes.chgdrs) {
      //如果chgdrs没有更新，则直接返回
      return;
    }

    let prevEntityId = this.getEntityId(changes.chgdrs.previousValue);
    let curEntityId = this.getEntityId(changes.chgdrs.currentValue);
    let requests = [
      this.updateChgdrConfigHandler(curEntityId),
      this.updateDataCodeFields(prevEntityId, curEntityId)
    ]
    forkJoin(requests).subscribe(data => {
      let chgdrDatas: ChangeDataQueryResult = changes.chgdrs.currentValue;
      if (!!chgdrDatas) {
        this.formatHeaders(chgdrDatas.headers);
      }

      //如果业务实体变更，则重置列信息
      if (curEntityId != null && curEntityId != prevEntityId) {
        this.resetColumnAndData(curEntityId, chgdrDatas.headers);
      } else {
        this.headers = chgdrDatas.headers;
      }
    }, err => {
      console.error("获取BE【" + curEntityId + "】的配置的业务变更日志的业务编号失败：", err);
      this.messageService.error(I18nUtil.instant("get-business-code-config-error", { errorMessage: ErrorUtil.getErrorMessage(err) }));
    });
  }

  /** 更新业务实体元数据 */
  private updateChgdrConfigHandler(entityId: string) {
    if (entityId) {
      return this.chgdrInnerService.getChgdrConfigHandler(entityId).pipe(tap(data => this.chgdrConfigHandler = data));
    } else {
      return of(this.chgdrConfigHandler);
    }
  }

  /**
   * 更新dataCodeField数据
   * @param prevEntityId 原BeId
   * @param curEntityId 当前BeId
   */
  updateDataCodeFields(prevEntityId: string, curEntityId: string) {
    if (curEntityId != null && curEntityId != prevEntityId) {
      //只有发生变更时才更新
      return this.chgdrService.getRootEntityDataCodeFields(curEntityId).pipe(tap(data => this.dataCodeFields = data || []));
    } else {
      //否则直接返回当前值
      return of(this.dataCodeFields);
    }
  }

  /**
   * 格式化headers。主要是解析其中的dataCode，将其转为Object
   * 使用dataCodeObj存放dataCode解析后的结果
   * dataCodeObj属性在ChangeDataHeader中不存在，此处通过强制赋值使用，并且只限于该处使用。
   */
  private formatHeaders(headers: ChangeDataHeader[]) {
    if (!headers) {
      return;
    }
    headers.forEach(header => {
      let dataCodeObj: any;
      this.formatDataCodeMap(header.dataCode);
      if (header.dataCode && header.dataCode.size == 1 && header.dataCode.has("_$dataCode")) {
        //兼容老数据
        //老数据dataCode为自由文本
        dataCodeObj = {};
        if (this.dataCodeFields && this.dataCodeFields.length > 0) {
          dataCodeObj[this.dataCodeFields[0].fieldLabelId] = header.dataCode.get("_$dataCode");
        }
      } else {
        dataCodeObj = MapUtil.convertMapToObject(header.dataCode);
      }
      (<any>header).dataCodeObj = dataCodeObj;
    })
  }

  /** 格式化业务主键Map */
  private formatDataCodeMap(dataCode: Map<string, string>): void {
    if (!dataCode || !this.chgdrConfigHandler) {
      return;
    }
    Array.from(dataCode.keys()).forEach(fieldLabelId => {
      let value = dataCode.get(fieldLabelId);
      let formatedValue = this.chgdrConfigHandler.formatMainObjectFieldValue(value, fieldLabelId);
      dataCode.set(fieldLabelId, formatedValue);
    })
  }

  private getEntityId(chgdrs: ChangeDataQueryResult): string {
    if (!chgdrs || !chgdrs.headers || chgdrs.headers.length == 0) {
      return null;
    } else {
      return chgdrs.headers[0].entityId;
    }
  }

  private resetColumnAndData(entityId: string, headers: ChangeDataHeader[]) {
    let columns: any[] = [
      { field: 'userName', width: 130, title: I18nUtil.instant("user-name-column-title") },
      { field: 'changeTime', width: 130, title: I18nUtil.instant("change-time-column-title"), formatter: { type: 'datetime', options: { format: I18nUtil.instant("change-time-column-format") } } },
      { field: 'operateType.name', width: 60, title: I18nUtil.instant("operation-type-title") },
      { field: 'dataId', width: 130, title: I18nUtil.instant("tech-pk-column-title"), visible: this.showDataId }];
    //{field: 'dataCode', width: 130, title: '业务编号'},
    // {field: 'reason', width: 130, title: '变更原因'},
    this.dataCodeFields.forEach((field) => {
      let column: DataColumn = {
        field: "dataCodeObj." + field.fieldLabelId,
        title: field.fieldName,
        width: 130
      };
      columns.push(column);
    })
    let opColumn = { title: I18nUtil.instant("operation-column-title"), width: 130, template: this.opCell, halign: 'center', align: 'center' };
    columns.push(opColumn);

    this.columns = columns;
    this.headers = headers;
  }

  onPageChanged($event) {
    this.pageChanged.emit($event);
  }

  onPageSizeChanged($event) {
    this.pageSizeChanged.emit($event);
  }

  clickShowChangeDetail(ctx) {
    let header: ChangeDataHeader = ctx.rowData;
    if (!header) {
      return;
    }
    if (!this.beforeShowChangeDetail) {
      //如果没有打开详情前回调，则按照默认方式打开
      this.showChangeDetail(header);
    } else {
      let event: BeforeShowChangeDetailEvent = new BeforeShowChangeDetailEvent();
      event.header = header;
      this.beforeShowChangeDetail(event).subscribe((isShow) => {
        if (isShow) {
          this.showChangeDetail(header);
        }
      }, (err) => {
        console.error("执行业务变更详情打开前事件报错：", err);
        this.messageService.error("执行业务变更详情打开前事件报错：" + ErrorUtil.getErrorMessage(err));
      })
    }
  }

  private showChangeDetail(header: ChangeDataHeader) {
    this.chgdrService.getChangeData(header.id, header.changeTime).subscribe(data => {
      if (!data) {
        this.notifyService.error(I18nUtil.instant("can-not-found-change-record-by-id", { headerId: header.id }));
      } else {
        this.currentChgdrData = data;
        this.chgdrInfoDialog.show();
      }
    }, error => {
      console.error("查询业务变更日志出错", error);
      this.messageService.error(I18nUtil.instant("query-change-data-record-error", { errorMessage: ErrorUtil.getErrorMessage(error) }));
    })
  }

  public get showChangeDetailBtn() {
    return I18nUtil.instant("show-change-detail-btn");
  }

  public get changeDetailDialogTitle() {
    return I18nUtil.instant("change-detail-dialog-title");
  }
}
