export class ChangeDataQueryParam {
    /** 用户ID */
    userId: string;
    /** 实体ID */
    entityId: string;
    /** 数据主键 */
    dataId: string;
    /** 数据编号 */
    dataCode: Map<string, string>;
    /** 变更时间-起始 */
    changeTimeStart: Date;
    /** 变更时间-结束 */
    changeTimeEnd: Date;
    /** 每页数量 */
    pageSize: number;
    /** 页码，从1开始 */
    pageIndex: number;
}