import { OperateType } from './operate-type';

export class ChangeDataRow {
    id: string;
    /**
     * header内码
     */
    headerId: string;
    /**
     * 实体编号
     */
    entityCode: string;
    /**
     * 数据主键
     */
    dataId: string;
    /**
     * 数据编号
     */
    dataCode: Map<string, string>;
    /**
     * 操作类型
     */
    operateType: OperateType;
    /**
     * 旧内容
     */
    oldContent: Map<string, string>;
    /**
     * 新内容
     */
    newContent: Map<string, string>;
    /**
     * 父数据主键
     */
    parentDataId: string;
}