export class ChgdrTreeNode {
    // 树配置中设置了idField的情况下，id会被自动赋值为data[idField]的值。所以在此设置必须设置为与data.id中相同。
    /** 节点ID，用于内部处理，请勿使用 */
    id: string;
    /** 节点名称 */
    name: string;

    /** 父节点 */
    parent: ChgdrTreeNode;
    /** 子节点列表 */
    children: ChgdrTreeNode[];

    /** 是否展开 */
    expanded: boolean;
    /** 是否隐藏 */
    hidden: boolean;

    /** 展示树结构用的数据实体 */
    data: any;
    chgdrRow: ChgdrDataRowNode;
    chgdrData: ChgdrData;
    isRoot: boolean;
}

export class ChgdrTreeBuilder {
    private chgdrData: ChgdrData;

    public build(chgdrData: ChgdrData): ChgdrTreeNode[] {
        this.chgdrData = chgdrData;
        if (chgdrData.entityNodes.length == 0) {
            console.warn("无变更日志");
            return [];
        }
        let rootEntityNode = chgdrData.entityNodes[0];
        let rootRows = rootEntityNode.rows;
        if (rootRows.length == 0) {
            console.warn("无根变更日志行");
            return [];
        }
        let rootRow = rootRows[0];

        let node = this.rowToNode(rootRow);
        node.isRoot = true;
        node.expanded = true;

        return [node];
    }

    private rowToNode(row: ChgdrDataRowNode): ChgdrTreeNode {
        const node: ChgdrTreeNode = new ChgdrTreeNode();
        node.id = Guid.newGuid();
        node.name = this.buildTreeNodeName(row);
        node.data = {
            id: node.id,
            name: node.name
        };
        node.chgdrRow = row;
        node.chgdrData = this.chgdrData;
        node.children = [];
        row.entityNodes.forEach(entityNode => {
            entityNode.rows.forEach(childRow => {
                const childNode: ChgdrTreeNode = this.rowToNode(childRow);
                childNode.parent = node;
                node.children.push(childNode);
            })
        })
        return node;
    }

    private buildTreeNodeName(row: ChgdrDataRowNode): string {
        let dataCodeName;
        if (row.dataCodes && row.dataCodes.length > 0) {
            dataCodeName = row.dataCodes[0].fieldValue;
        }
        if (dataCodeName) {
            return row.entityName + "-" + dataCodeName + "【" + row.operateType.name + "】";
        } else {
            return row.entityName + "【" + row.operateType.name + "】";
        }
    }
}