import { Injectable, Injector } from '@angular/core';
import { HttpService, SessionService } from '@ecp-caf/caf-common';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { GSPMetadataRTService } from '@gsp-lcm/metadatart-selector';
import { Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { ChangeDataHeader } from '../model/change-data-header';
import { ChangeDataQueryParam } from '../model/change-data-query-param';
import { ChgLogConfigField } from '../model/chgdr-config-field';
import { OperateType } from '../model/operate-type';
import { MapUtil } from '../util/map.util';

@Injectable({ providedIn: 'root' })
export class ChgdrMetadataService {
  private beMetadataCache: Map<string, GSPBusinessEntity> = new Map();

  constructor(private http: HttpService,
    private sessionService: SessionService,
    private metadataService: GSPMetadataRTService,
    private injector: Injector) { }

    /** 获取BE元数据 */
    public getGSPBusinessEntity(beId: string):Observable<GSPBusinessEntity>{
      if(this.beMetadataCache.has(beId)){
        let be = this.beMetadataCache.get(beId);
        return of(be);
      }else{
        return this.metadataService.GetMetadataRT(beId).pipe(map(metadata => {
          const be: GSPBusinessEntity = JSON.parse(metadata.content);
          this.beMetadataCache.set(beId, be);
          return be;
        }));
      }
    }

  // public getBeMetadataAndUdts(beId: string): Observable<any> {
  //   this.metadataService.GetMetadataRT(beId).subscribe((metadata) => {
  //     const be: GSPBusinessEntity = JSON.parse(metadata.content);
  //     let udtids:string[] = this.getAllUdtIds(be.MainObject);
  //     this.metadataService.
  //   });
  // }
}
