import { Injectable, Injector } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { HttpService, SessionService } from '@ecp-caf/caf-common';
import { Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ChgLogConfigEntity } from '../model/chgdr-config-entity';
import { ChgLogConfigField } from '../model/chgdr-config-field';
import { ChgdrInnerService } from './chgdr-inner.service';

const CHGDR_CONFIG_ROUTER = '/api/runtime/bcc/v1.0/chgdr/config';
const CHGDR_CONFIG_GET_BY_BIZOBJID_AND_MODELID = `${CHGDR_CONFIG_ROUTER}/{bizObjId}/{modelId}`;

const ServerIP = '/';
const chgdrUrl = `${ServerIP}api/runtime/chgdr/v1.0`;

@Injectable({ providedIn: 'root' })
export class ChgdrConfigService {

    constructor(private http: HttpService,
        private sessionService: SessionService,
        private injector: Injector) {
    }

    /**
     * 根据业务实体ID获取所有的配置信息
     */
    getBeChgdrConfig(beId: string): Observable<ChgLogConfigEntity[]> {
        let queryUrl = `${chgdrUrl}/config?beId=${beId}`;
        return (<Observable<ChgLogConfigEntity[]>><unknown>this.http.get(queryUrl)).pipe(tap(data => {
            let configEntities: ChgLogConfigEntity[] = data || [];
            configEntities.forEach(entity => {
                //处理字段顺序号
                let fields: Array<ChgLogConfigField> = entity.fields || [];
                //赋予老数据默认顺序号
                fields.forEach((field, index) => {
                    if (field.showIndex === undefined || field.showIndex === null) {
                        field.showIndex = index;
                    }
                });
                //按照顺序号排序
                entity.fields = fields.sort((a, b) => a.showIndex - b.showIndex);
                //根据排序结果重置顺序号
                fields.forEach((field, index) => {
                    field.showIndex = index;
                })
            })
        }));
    }
}