import { Injectable, Injector } from '@angular/core';
import { HttpService, SessionService } from '@ecp-caf/caf-common';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { GSPMetadataRTService } from '@gsp-lcm/metadatart-selector';
import { forkJoin, Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { ChangeDataHeader } from '../model/change-data-header';
import { ChangeDataQueryParam } from '../model/change-data-query-param';
import { ChgLogConfigEntity } from '../model/chgdr-config-entity';
import { ChgLogConfigField } from '../model/chgdr-config-field';
import { OperateType } from '../model/operate-type';
import { MapUtil } from '../util/map.util';
import { ChgdrConfigHandler } from './chgdr-config-handler';
import { ChgdrConfigService } from './chgdr-config.service';
import { ChgdrMetadataService } from './chgdr-metadata.service';

@Injectable({ providedIn: 'root' })
export class ChgdrInnerService {
    private beMetadataCache: Map<string, ChgdrConfigHandler> = new Map();

    constructor(private http: HttpService,
        private sessionService: SessionService,
        private metadataService: GSPMetadataRTService,
        private chgdrMetadataService: ChgdrMetadataService,
        private chgdrConfigService: ChgdrConfigService,
        private injector: Injector
    ) { }

    /** 获取BE元数据 */
    public getChgdrConfigHandler(beId: string): Observable<ChgdrConfigHandler> {
        if (this.beMetadataCache.has(beId)) {
            let be = this.beMetadataCache.get(beId);
            return of(be);
        } else {
            let requests = [];
            requests.push(this.chgdrMetadataService.getGSPBusinessEntity(beId));
            requests.push(this.chgdrConfigService.getBeChgdrConfig(beId));

            return forkJoin(requests).pipe(map(data => {
                const be = data[0];
                const entityConfigs: ChgLogConfigEntity[] = data[1] || [];
                const handler = new ChgdrConfigHandler(be, entityConfigs);
                this.beMetadataCache.set(beId, handler);
                return handler;
            }));
        }
    }
}
