import { Observable } from "rxjs";
import { ChangeDataHeader } from "./change-data-header";

export class BeforeShowChangeDetailEvent {
  /** 业务变更日志抬头信息 */
  header: ChangeDataHeader;
}

/**
 * 对于业务变更日志对话框的设置
 */
export class ChgdrListDialogConfig {
  /** 业务实体ID */
  entityId: string;
  /** 数据ID */
  dataId: string;
  /** 显示变更详情前的回调，返回false表示不使用内置界面显示变更详情。 */
  beforeShowChangeDetail: (event: BeforeShowChangeDetailEvent) => Observable<boolean>
}