import { Component, HostBinding, Injector, Input, OnChanges, OnInit, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { DialogComponent } from '@farris/ui-dialog';
import { MessagerService } from '@farris/ui-messager';
import { NotifyService } from '@farris/ui-notify';
import { ChgdrService } from '../service/chgdr.service';
import { ChangeDataHeader } from '../model/change-data-header';
import { ChangeDataQueryParam } from '../model/change-data-query-param';
import { ChangeDataQueryResult } from '../model/change-data-query-result';
import { OperateType } from '../model/operate-type';
import { ErrorUtil } from '../util/error.util';
import { TimeUtil } from '../util/time.util';
import { I18nUtil } from '../service/i18n-service';
import { BeforeShowChangeDetailEvent } from '../model/chgdr-ui-config';
import { Observable } from 'rxjs';

@Component({
  selector: 'chgdr-list',
  templateUrl: './chgdr-list.component.html',
  styleUrls: ['./chgdr-list.component.css']
})
export class ChgdrListComponent implements OnInit, OnChanges {
  @HostBinding('class.f-utils-fill-flex-column') baseCls = true;

  @Input()
  /** 业务实体ID */
  entityId: string;
  @Input()
  /** 数据ID */
  dataId: string;
  @Input()
  /** 变更时间-起始 */
  changeTimeStart: Date;
  @Input()
  /** 变更时间-结束 */
  changeTimeEnd: Date;

  @Input()
  /** 打开详情前的回调 */
  beforeShowChangeDetail: (event: BeforeShowChangeDetailEvent) => Observable<boolean>;

  private currentQueryParam: ChangeDataQueryParam;
  chgdrs: ChangeDataQueryResult;

  constructor(private chgdrService: ChgdrService,
    private messageService: MessagerService,
    private notifyService: NotifyService,
    private injector: Injector) {
  }

  ngOnInit() {
    let chgdrs = new ChangeDataQueryResult();
    chgdrs.total = 0;
    chgdrs.pageSize = 20;
    chgdrs.pageIndex = 1;
    chgdrs.headers = [];
    this.chgdrs = chgdrs;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.changeTimeStart && !this.changeTimeEnd) {
      //未录入变更时间范围，则查询最近一周的
      this.changeTimeEnd = TimeUtil.getEndTimeInDay(new Date());
      this.changeTimeStart = TimeUtil.getStartTimeInDay(new Date(new Date().getTime() - 7 * 24 * 60 * 60 * 1000));
    } else if (!this.changeTimeEnd) {
      //如果未设置截至时间，则与开始时间相同
      this.changeTimeEnd = TimeUtil.getEndTimeInDay(this.changeTimeStart);
    } else if (!this.changeTimeStart) {
      //如果未设置开始时间，则与截至时间相同
      this.changeTimeStart = TimeUtil.getEndTimeInDay(this.changeTimeEnd);
    }
    if (!!this.entityId) {
      let param = this.buildQueryParam();
      this.doQuery(param);
    }
  }

  public query() {
    let param = this.buildQueryParam();
    this.doQuery(param);
  }

  private buildQueryParam() {
    let param = new ChangeDataQueryParam();
    param.pageIndex = 1;
    param.pageSize = 20;
    param.entityId = this.entityId;
    param.dataId = this.dataId;
    param.changeTimeStart = this.changeTimeStart;
    param.changeTimeEnd = this.changeTimeEnd;
    return param;
  }

  private doQuery(param) {
    this.chgdrService.queryChangeDataHeader(param).subscribe(data => {
      this.currentQueryParam = param;
      this.chgdrs = data;
    }, error => {
      console.error("查询业务变更日志出错", error);
      this.messageService.error(I18nUtil.instant("query-change-data-record-error", { errorMessage: ErrorUtil.getErrorMessage(error) }));
    });
  }

  onPageChanged($event) {
    if (!$event) {
      return;
    }

    if ($event.pageIndex) {
      this.currentQueryParam.pageIndex = $event.pageIndex;
    }
    if ($event.pageSize) {
      this.currentQueryParam.pageSize = $event.pageSize;
    }
    this.doQuery(this.currentQueryParam);
  }

  onPageSizeChanged($event) {
    this.currentQueryParam.pageSize = $event;
    this.doQuery(this.currentQueryParam);
  }

  public get startTimeLabel() {
    return I18nUtil.instant("start-time-label");
  }

  public get endTimeLabel() {
    return I18nUtil.instant("end-time-label");
  }

  public get queryBtn() {
    return I18nUtil.instant("query-btn");
  }
}
