import { I18nUtil } from "../service/i18n-service";

export class OperateType {
    /** 新增 */
    static ADD: OperateType = new OperateType(0, "ADD", "新增");
    /** 修改 */
    static MODIFY: OperateType = new OperateType(1, "MODIFY", "修改");
    /** 删除 */
    static DELETE: OperateType = new OperateType(2, "DELETE", "删除");

    private constructor(public ordinal: number, public code: string, private defaultName: string) {
    }

    public get name() {
        return I18nUtil.instantOrDefault("operate-type-name-" + this.code, this.defaultName);
    }

    public static values(): OperateType[] {
        return [OperateType.ADD, OperateType.MODIFY, OperateType.DELETE];
    }

    public toJson() {
        return this.code;
    }

    public static parse(obj: any): OperateType {
        if (obj === undefined || obj === null) {
            return null;
        }
        if (obj instanceof OperateType) {
            return obj;
        }
        if (typeof obj === "string") {
            return OperateType.values().find(ot => ot.code == obj);
        }
        if (typeof obj === "number") {
            return OperateType.values().find(ot => ot.ordinal == obj);
        }
        if (!!obj.code) {
            return OperateType.values().find(ot => ot.code == obj.code);
        }
        return null;
    }
}