import { Component, Injector, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { GSPMetadataRTService } from '@gsp-lcm/metadatart-selector';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';
import { ChangeDataHeader } from '../model/change-data-header';
import { ChangeColumnItem, ChgdrData, ChgdrDataEntityNode, ChgdrDataRowNode } from './chgdr-data';
import { GSPAssociation, GSPCommonElement, GSPCommonObject, IGSPCommonElement, IGSPCommonObject } from '@gsp-bef/gsp-cm-metadata';
import { ChangeDataRow } from '../model/change-data-row';
import { ChgdrDataBuilder } from './chgdr-data-builder';
import { ChgdrTreeBuilder, ChgdrTreeNode } from './chgdr-tree-builder';
import { TreeTableComponent } from '@farris/ui-treetable';
import { ChgdrMetadataService } from '../service/chgdr-metadata.service';
import { ChgdrConfigService } from '../service/chgdr-config.service';
import { forkJoin } from 'rxjs';
import { ChgLogConfigEntity } from '../model/chgdr-config-entity';
import { ChgdrInnerService } from '../service/chgdr-inner.service';
import { I18nUtil } from '../service/i18n-service';

@Component({
  selector: 'chgdr-data-viewer',
  templateUrl: './chgdr-data-viewer.component.html',
  styleUrls: ['./chgdr-data-viewer.component.css']
})
export class ChgdrDataViewerComponent implements OnInit, OnChanges {
  @Input()
  data: ChangeDataHeader;
  @Input() showDataId: boolean = false;
  @ViewChild("treeTable")
  private treeTableComponent: TreeTableComponent

  treeConfig: any = {
    idField: 'id',
    columns: [
      { field: 'name', title: 'Name', width: 200 }
    ],
    /** 叶子节点图标 */
    leafIcon: 'f-icon f-icon-page-title-type text-info mr-2',
    /** 节点展开图标 */
    expandIcon: 'f-icon f-icon-file-folder-open text-info mr-2',
    /** 节点折叠图标 */
    collapseIcon: 'f-icon f-icon-file-folder-close text-info mr-2',
    showLevel: -1,
    disabled: false,
    init: false,
    selectValues: []
  };

  chgdrData: ChgdrData;
  treeData: ChgdrTreeNode[];
  currentNode: ChgdrTreeNode;

  constructor(private metadataService: GSPMetadataRTService,
    private injector: Injector,
    private chgdrMetadataService: ChgdrMetadataService,
    private chgdrConfigService: ChgdrConfigService,
    private chgdrInnerService: ChgdrInnerService) {
    }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!!this.data) {
      let beId = this.data.entityId;
      this.chgdrInnerService.getChgdrConfigHandler(beId).subscribe(handler=>{
        this.chgdrData = new ChgdrDataBuilder().buildChgdrData(this.data, handler);
        this.treeData = new ChgdrTreeBuilder().build(this.chgdrData);
        if (this.treeTableComponent) {
          //移除已选中项。
          //避免更换数据源后，treeGrid找不到之前的选中数据，导致控制台告警
          this.treeTableComponent.clearSelections();
        }
        setTimeout(() => {
          this.treeTableComponent.selectNode(this.treeData[0].id);
        }, 0);
      })
    }
  }

  public handleTreeSelection($event): void {
    const node: ChgdrTreeNode = $event.node;
    if (this.currentNode != null && this.currentNode.id == node.id) {
      //如果选中相同节点，直接返回
      return;
    }
    this.currentNode = node;
  }

  public get baseInfoSectionTitle(){
    return I18nUtil.instant("base-info-section-title")
  }

  public get changeDetailSectionTitle(){
    return I18nUtil.instant("change-detail-section-title");
  }

  public get  emptyDataText(){
    return I18nUtil.instant("empty-data-text");
  }
}
