import { Injectable, Injector } from '@angular/core';
import { HttpService, SessionService } from '@ecp-caf/caf-common';
import { Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { ChangeDataCompHeader, ChangeDataHeader } from '../model/change-data-header';
import { ChangeDataQueryParam } from '../model/change-data-query-param';
import { ChangeDataQueryResult } from '../model/change-data-query-result';
import { ChangeDataRow } from '../model/change-data-row';
import { ChgLogConfigField } from '../model/chgdr-config-field';
import { ChgdrItemQueryParam } from '../model/chgdr-item-query-param';
import { OperateType } from '../model/operate-type';
import { MapUtil } from '../util/map.util';

const ServerIP = '/';
const chgdrUrl = `${ServerIP}api/runtime/chgdr/v1.0`;

@Injectable({
  providedIn: 'root'
})
export class ChgdrService {
  private rootDataCodeFieldsMap: Map<string, ChgLogConfigField[]> = new Map();

  constructor(private http: HttpService,
    private sessionService: SessionService,
    private injector: Injector) { }

  public queryChangeDataHeader(currentQueryParam: ChangeDataQueryParam): Observable<any> {
    let obj: any = Object.assign({}, currentQueryParam);
    if (currentQueryParam.dataCode) {
      obj.dataCode = MapUtil.convertMapToObject(currentQueryParam.dataCode);
    }

    let json = JSON.stringify(obj);
    let queryParam = encodeURIComponent(json);
    let url = `${chgdrUrl}?queryParam=${queryParam}`;
    return this.http.get(url).pipe(map(data => {
      if (!data) {
        return data;
      }
      let queryResult: ChangeDataQueryResult = <ChangeDataQueryResult><unknown>data;
      queryResult && queryResult.headers && queryResult.headers.forEach(header => {
        //TODO 待删，兼容老数据格式
        if (typeof header.dataCode == "string") {
          header.dataCode = JSON.parse(header.dataCode);
        }

        header.dataCode = MapUtil.convertObjectToMap(header.dataCode);
        header.changeTime = this.toDate(header.changeTime);
        header.operateType = OperateType.parse(header.operateType);
      })
      return queryResult;
    }));
  }

  public getRootEntityDataCodeFields(beId: string): Observable<ChgLogConfigField[]> {
    if (!beId) {
      return of([]);
    }
    if (this.rootDataCodeFieldsMap.has(beId)) {
      //直接从缓存中获取
      return of(this.rootDataCodeFieldsMap.get(beId));
    } else {
      //获取结果并存入缓存
      let url = `${chgdrUrl}/rootDataCodeFields?beId=${beId}`;
      return (<Observable<ChgLogConfigField[]>><unknown>(this.http.get(url)))
        .pipe(tap(data => this.rootDataCodeFieldsMap.set(beId, data)));
    }
  }

  /** 将字符串或数字转为Date */
  private toDate(date: any): Date {
    if (typeof date == "string") {
      return new Date(date);
    } else if (typeof date == "number") {
      return new Date(date);
    } else {
      return date;
    }
  }

  /**
   * 查询业务变更日志详细数据。
   * @param id headerId
   * @param changeTime 变更时间，和header.changeTime保持一致
   */
  public getChangeData(headerId: string, changeTime: Date): Observable<any>
  public getChangeData(param: ChgdrItemQueryParam): Observable<any>
  public getChangeData(idOrParam: ChgdrItemQueryParam | string, changeTime?: Date): Observable<any> {
    if (idOrParam == null) {
      throw new Error("id can not be null");
    }
    if (idOrParam instanceof String || typeof idOrParam == "string") {
      return this.getChangeDataById(<string>idOrParam, changeTime);
    } else {
      return this.getChangeDataByParam(idOrParam);
    }
  }

  private getChangeDataById(id: string, changeTime: Date): Observable<any> {
    let json = JSON.stringify(changeTime);
    let queryParam = encodeURIComponent(json);
    let url = `${chgdrUrl}/${id}?changeTime=${queryParam}`;
    return this.http.get(url).pipe(map(data => {
      return this.transChangeDataFromResponse(data);
    }));
  }

  private getChangeDataByParam(param: ChgdrItemQueryParam): Observable<any> {
    if (param == null) {
      throw new Error("ChgdrItemQueryParam can not be null");
    }

    let requestParam: any = {};
    Object.assign(requestParam, param);
    //将Map转为Object，否则无法正常序列化
    requestParam.dataIds = MapUtil.convertMapToObject(param.dataIds);

    let url = `${chgdrUrl}`;
    return this.http.post(url, requestParam).pipe(map(data => {
      return this.transChangeDataFromResponse(data);
    }));
  }

  private transChangeDataFromResponse(data): ChangeDataCompHeader {
    if (!data) {
      return data;
    }
    let sh: ChangeDataCompHeader = <ChangeDataCompHeader><unknown>data;
    //TODO 待删，兼容老数据格式
    if (typeof sh.dataCode == "string") {
      sh.dataCode = JSON.parse(sh.dataCode);
    }
    sh.dataCode = MapUtil.convertObjectToMap(sh.dataCode);
    sh.changeTime = this.toDate(sh.changeTime);
    sh.operateType = OperateType.parse(sh.operateType);
    sh.rows = this.transChangeDataRowsFromResponse(sh.rows || []);
    return sh;
  }

  private transChangeDataRowsFromResponse(rows: ChangeDataRow[]): ChangeDataRow[] {
    if (!rows || rows.length == 0) {
      return rows;
    }

    rows.forEach(row => {
      if (!row) {
        return;
      }
      row.operateType = OperateType.parse(row.operateType);
      //TODO 待删，兼容老数据格式
      if (typeof row.dataCode == "string") {
        row.dataCode = JSON.parse(row.dataCode);
      }
      row.dataCode = MapUtil.convertObjectToMap(row.dataCode);
      row.oldContent = <Map<string, string>>MapUtil.convertObjectToMap(row.oldContent) || new Map();
      row.newContent = <Map<string, string>>MapUtil.convertObjectToMap(row.newContent) || new Map();
    });

    return rows;
  }
}
