import { ChangeDataRow } from './change-data-row';
import { OperateType } from './operate-type';

export class ChangeDataHeader {
    id: string;
    /**
     * 用户ID
     */
    userId: string;
    /**
     * 用户编号
     */
    userCode: string;
    /**
     * 用户名称
     */
    userName: string;
    /**
     * 实体ID
     */
    entityId: string;
    /**
     * 数据主键
     */
    dataId: string;
    /**
     * 数据编号
     */
    dataCode: Map<string, string>;
    /**
     * 变更时间
     */
    changeTime: Date;
    /**
     * 变更类型
     */
    operateType: OperateType;
    /**
     * 变更原因
     */
    reason: string;
    /**
     * 分组ID
     */
    groupId: string;

    /**
     * 变更行列表
     */
    rows: ChangeDataRow[];
}

export class ChangeDataCompHeader extends ChangeDataHeader {
    extEntityRows: Map<String, ChangeDataRow[]>;
}