import { OperateType } from '../model/operate-type';

/** 基本信息 */
export class ChgdrData {
    entityName: string;
    userName: string;
    operateType: OperateType;
    reason: string;
    dataId: string;
    dataCode: Map<string, string>;
    dataCodes: DataCodeField[];
    changeTime: Date;
    /** 本变更日志的根节点 */
    entityNodes: ChgdrDataEntityNode[];
}

/** 发生变更的实体节点 */
export class ChgdrDataEntityNode {
    entityCode: string;
    entityName: string;
    rows: ChgdrDataRowNode[];
}

/** 发生变更的行 */
export class ChgdrDataRowNode {
    id: string;
    entityCode: string;
    entityName: string;
    parentDataId: string;
    dataId: string;
    dataCode: Map<string, string>;
    dataCodes: DataCodeField[];
    operateType: OperateType;
    changes: ChangeColumnItem[];
    /** 发生变更的行的下级实体 */
    entityNodes: ChgdrDataEntityNode[];
}

export class ChangeColumnItem {
    fieldLabel: string;
    fieldName: string;
    oldValue: string;
    newValue: string;
    /** 是否关联字段 */
    hasAssociation: boolean;
    /** 原始关联字段 */
    originalColumnItem: ChangeColumnItem;
    /** 是否关联带出字段 */
    isAssociationRefField: boolean;
    /** 关联带出字段所属的字段 */
    belongFieldLabelId: string;
    /** 如果当前字段为关联字段，则属性为当前字段的关联带出字段 */
    children: ChangeColumnItem[] = [];
}

export class DataCodeField {
    fieldLabelId: string;
    fieldName: string;
    fieldValue: string;
}