import { Pipe, PipeTransform, Injectable } from '@angular/core';
import { TranslateDefaultParser, TranslateParser, TranslateService } from '@ngx-translate/core';
import { LangDataEn } from './i18n/en';
import { LangDataZhChs } from './i18n/zh-CHS';
import { LangDataZhCht } from './i18n/zh-CHT';

let I18nServiceInstance: any;

let langResourceMap: Map<string, any> = new Map();
langResourceMap.set("en", LangDataEn);
langResourceMap.set("zh-CHS", LangDataZhChs);
langResourceMap.set("zh-CHT", LangDataZhCht);

let currentLang: string = localStorage.getItem('languageCode');
if (!currentLang) {
  currentLang = "zh-CHS";
  console.log("未找到设置的语言，默认使用" + currentLang);
}

let parser: TranslateParser = new TranslateDefaultParser();

export class I18nUtil {
  public static instant(key: string, param?: any): string {
    let statement = I18nUtil.getCurrentLangResource()[key];
    if (statement === undefined || statement === null) {
      return key;
    }

    let result = parser.interpolate(statement, param);
    if (result === undefined || result === null) {
      return key;
    }

    return result;
  }

  private static getCurrentLangResource() {
    return langResourceMap.get(currentLang) || {};
  }

  public static instantOrDefault(key: string, defaultText: string) {
    let statement = I18nUtil.getCurrentLangResource()[key];
    if (statement === undefined || statement === null) {
      return defaultText;
    }

    let result = parser.interpolate(statement);
    if (result === undefined || result === null) {
      return defaultText;
    }

    return result;
  }
}

@Injectable({ providedIn: 'root' })
export class I18nService {

  constructor() {
  }

  public instant(key: string, param?: any): string {
    return I18nUtil.instant(key, param);
  }

  private getCurrentLangResource() {
    return langResourceMap.get(currentLang) || {};
  }

  public static staticInstant(key: string, defaultText: string) {
    return I18nUtil.instantOrDefault(key, defaultText);
  }
}
